/* !! - - -  */
/* !! SPメニューボタン */

//!!　開閉ボタン
#btn-toggle-menu{
  $dly:100ms;
  position: absolute;
  right: 0px;top: 0px;
  height: 60px;width: 60px;
  background: #eee;
  z-index: 1;
  border-radius: 0;
  cursor: pointer;
  @include flex;
  @include pc{
    display: none;  
  }//@pc
  -webkit-tap-highlight-color:rgba(0,0,0,0);
  .inner{
    transition: 100ms;
    width: 30px;
    height: 28px;
    position:absolute;
    top: 15px;
    left: 15px;
    //background: #ccc;
    padding: 0;
  }
  &:active{
    background: #ccc;
    .inner{
      transform: scale(0.8);
      
    }
  }
  .line{
    position: absolute;
    left: 0px;
    height: 0px;
    width: 30px;
    transition-duration: 300ms;
    transition-timing-function: $ease1 ;
    opacity: 1;
    &:after{
      transition-duration: 400ms;
      transition-timing-function: $ease1 ;
      position: absolute;top: 0;left: 0;
      @include transform(translateX(100px));
      content: "";
      background: $text;
      width: 30px;
      height: 4px;
      border-radius: 10px;
      willchange:transform;
    }
  }
  .rdy1{top: 0px;

    &:after{
      
      transition-delay: $dly*0 ;
    }
  }
  .rdy2{top: 12px;

    &:after{
      transition-delay: $dly*1 ;
    }
  }
  .rdy3{top: 24px;
    &:after{
      transition-delay: $dly*2 ;
    }
  }

  .close1{
    top: 13px;
    left: 1px;
    @include transform(rotate(45deg));
    &:after{
      transition-delay:0 ;
    }
  }
  .close2{
    top: 13px;
    left: -1px;
    @include transform(rotate(-45deg));
    &:after{
      transition-delay: $dly*1  ;
    }
  }
  
  .rdy1,.rdy2,.rdy3{
    &:after{
      @include transform(translateX(0px));
    }
  }
  .close1,.close2{
    opacity: 0;
    &:after{
      @include transform(translateX(0px));
    }
  }
  
  .sp-menu-shown &{
    .rdy1,.rdy2,.rdy3{
      opacity: 0;
      &:after{
        //@include transform(translateX(100px));
      }
    }
    .close1,.close2{
      opacity: 1;
      &:after{
        @include transform(translateX(0));
      }
    }
  }
}


