    &[data-pcode='3101572']:after{border-color: #00b7a8;}
    &[data-pcode='3101581']:after{border-color: #fdb33c;}
    &[data-pcode='3101569']:after{border-color: #0eb95b;}
    &[data-pcode='3101585']:after{border-color: #fdd33c;}
    &[data-pcode='3101526']:after{border-color: #fdd33c;}
    &[data-pcode='3101554']:after{border-color: #fdd33c;}


  }
}


