}


/* !!------------------------------------ */
/* !! 商品詳細画面共通 */

.cart-origin{
  display: none;
}

.ec-productRole{
  padding: 0;
}


//!! - お気に入り登録
.favorite-btn-wrap{
  position: absolute;
  top: 0;right: 0;
  //background: #eee;
  @include sp{
    top: auto;
    bottom: 0px;
    right: 0;
    width: 100%;  
    position: static;
    margin-top: 15px;
  }//@sp
  .ec-productRole__btn{
    margin-bottom: 0;width: auto !important;
    button{
      margin: 0 !important;
      margin-left: auto !important;
      margin-right: 0 !important;
      @include sp{
        margin: 0 auto !important;  
      }//@sp
    }
    .ec-blockBtn--cancel{
      width: auto;
      height: 36px !important;
      font-size: 14px;
      letter-spacing: .05em;
      background: $yellow;
      border: 2px solid #fff !important;
      color: #63500e !important;
      line-height: 1;
      @include sp{
        height: 32px !important;
        font-size: 12px;  
        width: auto !important;
      }//@sp
    }
  }
}


//!! - 商品タイトル
#product-header{
  position: relative;
  @include sp{
    padding: 12*$px 15*$px;  
  }//@sp
  .tag{
    @include flex;
    justify-content: flex-start;
    li{
      height: 22px;
      border-radius: 50px;
      background: #eee;
      font-size: 12px;
      color: $pink;
      @include flex-centering;
      font-weight: bold;
      padding: 0 15px;
      line-height: 1;
      margin-right: 10px;
      letter-spacing: .05em;
      @include sp{
        font-size: 11px;
        height: 22px;
        padding: 0 10px;
        margin-right: 5px;  
      }//@sp
      &.light{
        background: #ffdcef;
      }
      &.heavy{
        background: $pink ;
        color: #fff;
      }
    }
  }
  .ttl{
    font-size: 15px;
    line-height: 1;
    margin: 15px 0;
    letter-spacing: .1em;
    @include sp{
      font-size: 12px;
      margin: 0;
      margin-top: 12*$px;
    }//@sp
    strong{
      font-size: 1.66em;
      font-weight: bold;
      display: block;
      margin-bottom: 10px;
      line-height: 1.4;
      @include sp{
        font-size: 1.5em;
        letter-spacing: 0.05em;  
      }//@sp
    }
  }
}

@mixin cart-trns{
  transition: 300ms;
}

#product-wrap{
  //background: #eee;
  margin-bottom: 50px;
  @include flex;
  position: relative;
  
  .product-img{
    width: 465px;
    height: 436px;
    border-radius: 12px;
    position: relative;
    @include flex-centering;
    @include sp{
      height: 281*$px;
      margin-bottom: 50px;
      border-radius: 0 !important;
    }//@sp
    &:before{
      content:"";
      position: absolute;
      top: 0;left: 0;
      width:100%;
      height:100%;
      z-index: 0;
      border-radius: 12px;
      opacity: .2;
      @include sp{
        border-radius: 0;  
      }//@sp
    }
    &[data-pcode='3101561']:before{@include bg_3101561;}
    &[data-pcode='3101572']:before{@include bg_3101572;}
    &[data-pcode='3101999']:before{@include bg_3101999;}
    &[data-pcode='3101581']:before{@include bg_3101581;}
    &[data-pcode='3101569']:before{@include bg_3101569;}
    &[data-pcode='3101590']:before{@include bg_3101590;}
    &[data-pcode='3101585']:before{@include bg_3101585;}
    &[data-pcode='3101526']:before{@include bg_3101526;}
    &[data-pcode='3101554']:before{@include bg_3101554;}

    .label{
      width: 116px;
      height: 116px;
      position: absolute;
      top: -20px;right: -10px;
      z-index: 2;
      padding: 0;
      @include flex-centering;
      @include sp{
        width: 80*$px;
        height: 80*$px;  
        right: 15*$px;
        top: 15*$px;
      }//@sp
      >img{
        height: auto;
        width: 150%;
        max-width: 200px;
      }
    }
    >img{
      height: 70%;
      width: auto;
      position: relative;
      z-index: 1;
      @include sp{
        height: 75%;  
      }//@sp
    }
    .content-img{
      background: #fff;
      position: absolute;
      bottom: 20px;
      right: 20px;
      width: 110px;
      height: 110px;
      border-radius: 300px;
      z-index: 2;
      opacity: 1;
      @include flex-centering;
      @include sp{
        $sz : 80*$px;
        width: $sz;
        height: $sz;
        bottom: 20*$px;
        right: 20*$px;
      }//@sp
      img{
        width: 200% !important;
        height: 200% !important;
        max-width: 1000px;
      }
    }

  }
  
  //!! - カートボタン表示部
  .product-cart-wrap{
    width: 465px;
    height: 436px;
    //background: #EEE;
    @include sp{
      padding: 0 15*$px;
      height: auto;
      width: 100%;
    }//@sp
  }
  
  .product-cart-header{
    //background: #EEE;
    @include flex;
    font-size: 19px;
    position: relative;
    white-space: nowrap;
    @include sp{
      font-size: 14px;  
    }//@sp
    li{
      cursor: pointer;
      background: #DCDDDD;
      width: 50%;
      height: 50px;
      border-radius: 12px 12px 0 0;
      @include flex-centering;
      font-weight: bold;
      position: relative;
      letter-spacing: .05em;
      @include cart-trns;
      @include sp{
        height: 50px;  
      }//@sp
      &.subscription{
        body[data-type="subscription"] & {
          background: $pink;
          color: #fff;
        }
      }
      &.onestop{
        body[data-type="onestop"] & {
          background: $blue;
          color: #fff;
        }
      }
      .comment{
        font-size: 16px;
        position: absolute;
        top: -10px;left: 0;
        width: 100%;
        text-align: center;
        @include transform(translateY(-100%));
        line-height: 1;
        color: #000;
        opacity: 1;
        @include cart-trns;
        @include sp{
          font-size: 14px;  
        }//@sp
        body[data-type="onestop"] & {
          opacity: 0;
        }
        b{
          font-size: 1.8em;
          @include BILO;
          font-weight: bold;
          padding: 0 5px;
          color: $pink;
        }
        i{
          font-size: 12px;
          display: inline-block;
          @include transform(translateY(-40%));
        }
      }
    }
  }
  
  .product-cart-inner{
    position: relative;
    height: 386px;
    width: 100%;
    @include sp{
      height: auto
    }//@sp
    >div{
      @include flex-centering;
      //justify-content: flex-start;
      justify-content: space-between;
      margin: 0;
      margin-bottom: auto;
      width: 100%;
      height: 386px;
      border: 4px solid #DCDDDD;
      padding: 20px 0;
      border-radius: 0 0 12px 12px;
      position: absolute;
      top: 0;left: 0;
      background: #fff;
      opacity: 0;
      pointer-events: none;
      overflow: hidden;
      @include sp{
        padding: 10px 0;
        height: auto;  
      }//@sp
      @include sp{
        position: relative;
        display: none !important;
        height: auto;
      }//@sp
      //@include cart-trns;
      &.product-cart-teiki{
        body[data-type="subscription"] & {
          border-color: $pink;
          opacity: 1;
          pointer-events: auto;
          @include sp{
            display: block !important;  
          }//@sp
          
        }
      }
      &.product-cart-onestop{
        body[data-type="onestop"] & {
          border-color: $blue;
          opacity: 1;
          pointer-events: auto;
          
          @include sp{
            display: block !important;  
          }//@sp
        }
      }
      >div{
        width: 100%;
      }
    }
    
    .row-base-price{
      padding: 0 20px;
      margin-bottom: 10px;
      width: 100%;
      @include sp{
        padding: 0 10px;
      }//@sp
      p{
        font-size: 16px;
        font-weight: bold;
        line-height: 1;
        text-align: right;
        @include sp{
          font-size: 14px;  
        }//@sp
      }
      i{
        position: relative;
        display: inline-block;
        &:after{
          content: "";
          width: 100% ;
          height: 3px;
          display: block;
          background: $red;
          position: absolute;
          top: 50%;@include transform(translateY(-50%));
          opacity: .5;
        }
      }
    }
    //!! - 定期価格
    .label-tag{
      background: $pink;
      height: 24px;
      color: #fff;
      font-size: 12px;
      white-space: nowrap;
      @include flex;
      justify-content: center;
      align-items: center;
      font-weight: bold;
      letter-spacing: .05em;
      line-height: 1;
      vertical-align: middle;
      border-radius: 50px;
      @include sp{
        font-size: 10px;
        height: 20px;
        
      }//@sp
      .price{
        @include BILO;
        font-weight: bold;
        font-size: 1.2em;
        line-height: 1;
        @include flex;
        align-items: center;
        i{
          font-size: 18px;
          line-height: 1;
          position: relative;
          top: -1px;
        }
      }
    }
    .row-sbsc-price1,.row-try-price{
      background: $lite-yellow;
      padding: 15px 20px;
      @include flex;
      align-items: center;
      @include sp{
        padding: 10*$px;
        flex-wrap: nowrap;
      }//@sp
      .label{
        display: block;
        padding: 0;
        height: 100% !important;
        color: $pink;
        @include flex;
        justify-content: flex-start;
        align-items: center;
        flex-wrap: nowrap;
        p{
          background: #fff;
          height: 50px;
          @include flex-centering;
          padding: 0 10px;
          border-radius: 12px;
          font-size: 15px;
          font-weight: bold;
          line-height: 1;
          margin-right: 8px;
          @include sp{
            font-size: 12*$px;
            height: 40*$px;
            margin-right: 5*$px;
          }//@sp
          >span{
            line-height: 1.2;
          }
          .price{
            font-size: 18px;
            line-height: 1;
            @include BILO;
            font-weight: bold;
            @include sp{
              font-size: 1.2em;  
            }//@sp
            i{
              font-size: 1.4em;
            }
          }
        }
      }
      >.price{

        p{
          font-size: 16px !important;
          font-weight: bold;
          line-height: 1;
          white-space: nowrap;
          @include textshadow;
          @include sp{
            font-size: 13*$px;  
          }//@sp
        }
        i{
          font-size: 50px !important;
          @include BILO;
          font-weight: bold;
          color: $red;
          margin-right: 5px;
          @include sp{
            font-size: 36*$px !important;  
          }//@sp
        }
      }
    }

    .row-sbsc-price2{
      padding: 15px 20px;
      @include sp{
        padding: 12*$px 10*$px;  
      }//@sp
      .label-tag{
        width: 150px;
        @include sp{
          white-space: nowrap;
          width: 130*$px;
        }//@sp
      }
      ul{
        @include flex;
        justify-content: flex-end;
        li{
          @include flex;
          //justify-content: flex-end;
          align-items: center;
          margin-bottom: 10px;
          height: 24px;
          width: 320px;
          @include sp{
            //justify-content: flex-end;
            margin-bottom: 6*$px;  
          }//@sp
          &:last-of-type{margin-bottom: 0;}
          p{
            font-size: 16px;
            color: $pink;
            font-weight: bold;
            //margin-left: 1em;
            margin: 0;
            height: 100%;
            margin-top: -.8em;
            //width: 160px;
            text-align: right;
            white-space: nowrap;
            @include sp{
              font-size: 15*$px;
              //width: 150*$px;
              margin-top: -.3em;
            }//@sp
            i{
              font-size: 2em;
              
              line-height: 1;
              @include BILO;
              font-weight: bold;
              margin-right: 5px;
            }
          }
        }
      }
      
    }
    
    //!! - 1回のみお届け
    .product-cart-onestop{
      @include flex-centering;
      justify-content: space-between;
      padding: 0;
      >div{
        width: 100%;
      }
      .label-tag{
        width: 110px;
        height: 30px; 
        font-size: 14px;
        margin-right: 10px;
        @include sp{
          height: 24px;
          font-size: 12px;
          width: 100*$px;  
        }//@sp
      }
    }
    //!! - - 通常購入
    .base-price-wrap{
      margin-bottom: 10px;
      padding-top: 20px;
      @include sp{
        padding: 10*$px 0;  
      }//@sp
      .row-base-price{
        height: auto;
        width: 100%;
        .label{
          padding: 0;
        }
        @include flex;
        align-items: center;
        @include sp{
          align-items: flex-start;  
        }//@sp
        .price{
          p{
            @include flex;
            align-items: center;
            @include sp{
              display: block;
              span{
                display: block;
                font-size: 12px;
                margin-bottom: 6px;
              }  
            }//@sp
          }
          .base{
            font-size: 24px;
            margin-left: 5px;
            margin-top: -.2em;
            &:after{
              content: "";
              display: none;          
            }
          }
        }
      }
    }//.base-price-wrap
    .row-info{
      padding: 0 20px;
      margin-top: 15px;
      @include flex;
      justify-content: flex-end;
      font-size: 12px;
      @include sp{
        padding: 0 10*$px;
        justify-content: space-between;  
      }//@sp
      >p{
        //border: 1px solid $pink;
        background: rgba($pink,.1);
        border-radius: 50px;
        color: $pink;
        margin-left: 10px;
        height: 24px;
        line-height: 1;
        @include flex;
        justify-content: center;
        align-items: center;
        white-space: nowrap;
        padding: 0 10px;
        font-weight: bold;
        @include sp{
          margin-left: 10*$px;
          &:first-of-type{margin-left: 0;}
          flex-grow: 2;
          padding: 0*$px;
          font-size: 11*$px;  
        }//@sp
      }
    }

    //!! - - お試し購入
    .try-price-wrap{
      background: $lite-yellow;
      padding: 20px 0;
      padding-top: 10px;
      height: 100%;
      position: relative;
      @include pc{
        @include flex-centering;
        justify-content: flex-start;
        align-items: flex-start;  
      }//@pc
      @include sp{
        padding-bottom: 10*$px;  
      }//@sp
      .ec-numberInput{
        pointer-events: none;
        opacity: .5;
      }
      .label_sale{
      
        margin-bottom: 10px;
        text-align: center;
        width: 100%;
        padding: 0 30px;
        display: none;
        @include pc{
          padding: 0 50px;
          position: absolute;
          top: 0;left: 0;
          @include transform(translateY(-50%));  
        }//@pc
        
      }
      
    }
    .row-try-price{
      padding: 0 20px;
      width: 100%;
      @include sp{
        padding: 0 10*$px;  
      }//@sp
      .label{
        @include sp{
          display: block;  
        }//@sp
        p{
          height: 40px !important;
          font-size: 14px;
          position: relative;
          @include sp{
            height: auto;
            font-size: 10px;
            height: 24px !important;
            width: 100*$px;  
            padding: 0;
            margin-top: 5px;
            br{
              display: none;
              
            }  
          }//@sp
        }
      }
      .price{
        height: 40px;
        position: relative;
        @include flex;
        align-items: center;
        p{
          margin-top: -.4em;
        }
      }
      .delivery-fee{
        position: absolute;
        top: 0;right: 0;
        white-space: nowrap;
        line-height: 1;
        font-size: 11px;
        height: 18px;background: #fff;
        @include flex-centering;
        width: 62px;
        border-radius: 50px;
        text-shadow: none;
      }
    }
    .row-try-txt{
      width: 100%;
      padding: 0 20px;
      &:empty{
        display: none;
      }
      .try-txt{
        @include flex;
        
        justify-content: flex-end;
        @include pc{
          padding: 10px 0;  
        }//@pc
        p{
          background: $red;
          width: 200px;
          color: #FFF;
          font-weight: bold;
          text-align: center;
          font-size: 12px;
          line-height: 1;
          padding: 3px 0;
          height: 22px;
          @include flex;
          align-items: center;
          justify-content: center;
          line-height: 1;
          i{
            font-size: 1.4em;
          }
        }
      }
    }
    .row-cart-setion{
      margin-top: auto;
      margin-bottom: 0;
      margin-top: 20px;
      width: 100%;
      form{
        height: 54px;
      }
      #quantity{
        height: auto !important;
        margin-bottom: auto !important;
      }
      .ec-productRole__actions{
        @include flex;
        height: auto !important;
        align-items: center;
        margin: auto 0 !important;
        .ec-numberInput{
          height: auto;
        }
      }
      .productRole__btn{
        margin-bottom: 0;
      }
    }
    
    .row-coupon-code{
      padding: 0 20px;
      margin: 10px 0;
      @include sp{
        margin: 10*$px 0;
        padding: 0 10*$px;
      }//@sp
      .coupon{
        @include flex;
        background: #fff;
        border-radius: 12px;
        height: 40px;
        >p{
          padding:0 10px;
          width: 50%;
          @include flex-centering;
          align-items: flex-start;
          font-size: 14px;
          font-weight: bold;
          line-height: 1.2;
          @include sp{
            width:60%;
            padding: 0 10*$px;  
            font-size: 12*$px;
            white-space: nowrap;
            align-items: center;
          }//@sp
          i{
            font-size: .8em;
            @include sp{
              font-size: .7em;  
            }//@sp
          }
        }
        div{
          width: 50%;
          @include sp{
            width: 40%;  
            font-size: 25*$px;
          }//@sp
          @include flex-centering;
          @include BILO;
          font-size: 30px;
          letter-spacing: .1em;
          line-height: 1;
          border-left: 2px solid $lite-yellow;
        }
      }
    }
    
  }
  
  p.note{
    position: absolute;
    bottom: -5px;
    @include transform(translateY(100%));
    width: 100%;
    font-size: 13px;
    opacity: 0;
    body[data-type="onestop"] & {
      opacity: 1;
    }
    &.onestop{
      body[data-pcode="3101590"] & {
        display: none;
      }
    }
  }
  
  .row-banner{
    @include flex;
    justify-content: flex-end;
    padding: 0 20px;
    @include sp{
      padding:0 10*$px;  
    }//@sp
    .teiki-banner{
      height: 36px;border: 1px solid $pink;
      width: 320px;
      @include flex-centering;
    }
  }
  
  //!! - カートに入れる
  .row-cart-setion{
    padding: 0 20px;
    height: 54px;
    margin-top: auto;
    margin-bottom: 6px;
    @include sp{
      margin-top: 20*$px !important;
      height: auto;
      padding: 0 10*$px;  
    }//@sp
    >.cart-in-section form{
      @include flex;
      justify-content: flex-end;
      align-items: center;
      @include sp{
        justify-content: space-between;  
      }//@sp
    }
    .ec-productRole__actions{
      padding: 0;
      padding-right: 10px;
      font-weight: bold;
      #quantity,.alt-quantity{
        width: 50px;
        height: 36px;
        border-width: 3px;
        border-radius: 8px;
        padding: 0;
        text-align: left;
        padding: 5px;
        padding-left: 10px;
        margin-left: 10px;
        font-size: 16px;
        text-align: center;
        @include sp{
          margin-left: 5*$px;  
        }//@sp
      }
      .alt-quantity{
        display: inline-block;
        //padding: 0 !important;
        width: auto;
        padding:0 10px;
    
      }
    }
    .ec-productRole__btn{
      min-width: 0;
      width: auto;
      margin-bottom: 0;
    }
    button.add-cart{
      @extend %btn_CTA;
      width: 250px;
      margin-bottom: 0;
      &:before{
        //content: "\e93a" !important;
      }
      @include sp{
        width: 195*$px;  
      }//@sp
    }
  }
  
  .row-checkbox{
    line-height: 1;
    padding: 0 20px;
    text-align: right;
    margin-bottom: 0;
    margin-top: 5px;
    font-size: 14px;
    //display: none;
    @include sp{
      margin: 15*$px 0 10*$px 0;
      
      padding: 0 10*$px;
      text-align: center;
    }//@sp
    a{
      color: $blue;
    }
    input{
      margin-right: 10px;
    }
  }
  

}


/* !! 定期カートボタン */
.product-cart-teiki{
  .add-cart{
    pointer-events: none;
    opacity: .5;
    .mention_checked & {
      opacity: 1;
      pointer-events: auto;
    }
  }
}

.mention-subscriotion{
  background: rgba($pink,.1);
  padding: 30px;
  border-radius: 12px;
  margin-top: -20px;
  position: relative;
  display: none;
  @include sp{
    //border-radius: 0;
    padding: 20*$px;
    margin: 15*$px;  
  }//@sp
  h3{
    font-size: 18px;
    position: absolute;
    top: 0;left: 0;
    width: 100%;
    text-align: center;
    @include transform(translateY(-50%));
    font-weight: bold;
    @include textshadow();
  }
  h4{
    font-weight: bold;
